import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import {
  Statistic,
  Table,
  Header,
  Image,
  Icon,
  Grid
} from 'semantic-ui-react';


type Statistics = {
  countries: number;
  diseases: number;
  records: number;
  publicServants: number;
};

type Servant = {
  email: string;
  name: string;
  surname: string;
  records: number;
  department: string;
}

type Doctor = {
  email: string;
  name: string;
  surname: string;
  specializations: string[];
  degree: string;
}

type Disease = {
  name: string;
  pathogen: string;
  infected: number;
  first_enc_date: string;
  country: string;
};

type DiseaseType = {
  name: string;
  diseases: number;
}


function capitalize(s: string) {
  return s.charAt(0).toUpperCase() + s.slice(1);
}


function Stats() {
  const [stats, setStats] = useState<Statistics>({countries: 0, diseases: 0, records: 0, publicServants: 0});
  const [servants, setServants] = useState<Servant[]>([]);
  const [infectiousDiseases, setInfectiousDiseases] = useState<Disease[]>([]);
  const [newestDiseases, setNewestDiseases] = useState<Disease[]>([]);
  const [diseaseTypes, setDiseaseTypes] = useState<DiseaseType[]>([]);
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [images, setImages] = useState<string[]>([]);

  useEffect(() => {
    fetch("/api/v1/stats")
      .then((res) => res.json())
      .then((data) => setStats(data));

    fetch("/api/v1/stats/top/servants")
      .then((res) => res.json())
      .then((data) => setServants(data));

    fetch("/api/v1/stats/top/diseases/infected")
      .then((res) => res.json())
      .then((data) => setInfectiousDiseases(data));

    fetch("/api/v1/stats/top/diseases/newest")
      .then((res) => res.json())
      .then((data) => setNewestDiseases(data));

    fetch("/api/v1/stats/top/diseases/types")
      .then((res) => res.json())
      .then((data) => setDiseaseTypes(data));

    fetch("/api/v1/stats/top/doctors")
      .then((res) => res.json())
      .then((data) => setDoctors(data));

    fetch("https://randomuser.me/api/?results=10")
      .then((res) => res.json())
      .then((data) => {
        let images: string[] = [];
        data.results.map((res: any) => images.push(res.picture.medium));
        setImages(images); });
  }, []);


  return (
    <>
    <Grid divided='vertically' style={{"marginTop": "2em"}}>
      <Grid.Row columns={3} centered>
        <Grid.Column width="5" textAlign="center">
          <Header as="h3" icon>
            <Icon name='clipboard outline'/>
            Top activists
            <Header.Subheader>
              Servants with highest number of records created
            </Header.Subheader>
          </Header>

          <Table collapsing basic='very' style={{"margin": "2em auto 0 auto"}}>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Public servant</Table.HeaderCell>
                <Table.HeaderCell>Records</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {servants && servants.map((servant: Servant, i: number) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as='h4' image>
                      <Image rounded src={images[i]} />
                      <Header.Content as={Link} to={`/users/${servant.email}`}>
                        {`${servant.name} ${servant.surname}`}
                        <Header.Subheader>{servant.department}</Header.Subheader>
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{servant.records}</Table.Cell>
                </Table.Row>
              ))}
              
            </Table.Body>
          </Table>
        </Grid.Column>

        <Grid.Column width="11" textAlign="center">
          <Header as="h3" icon>
            <Icon name='user md' />
            Best specialists
            <Header.Subheader>
              Doctors who specialize in many disease types
            </Header.Subheader>
          </Header> 

          <Table collapsing basic='very' style={{"margin": "2em auto 0 auto"}}>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Doctor</Table.HeaderCell>
                <Table.HeaderCell>Number</Table.HeaderCell>
                <Table.HeaderCell>Specializations</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {doctors && doctors.map((doctor: Doctor, i: number) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as='h4' image>
                      <Image rounded src={images[i + 5]} />
                      <Header.Content as={Link} to={`/users/${doctor.email}`}>
                        {`${doctor.name} ${doctor.surname}`}
                        <Header.Subheader>{doctor.degree}</Header.Subheader>
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{doctor.specializations.length}</Table.Cell>
                  <Table.Cell>
                    {doctor.specializations.reduce((spec, s, i) => 
                      capitalize(spec) + 
                      ((i < doctor.specializations.length) ? ", " : "") +
                      capitalize(s)
                    )}
                  </Table.Cell>
                </Table.Row>
              ))}
            </Table.Body> 
          </Table>
        </Grid.Column>
      </Grid.Row>
    </Grid>

    <Statistic.Group widths="four" size="large" style={{"marginTop": "2em"}}>
      <Statistic>
        <Statistic.Value>{stats.records}</Statistic.Value>
        <Statistic.Label>Records</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.countries}</Statistic.Value>
        <Statistic.Label>Countries</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.diseases}</Statistic.Value>
        <Statistic.Label>Diseases</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.publicServants}</Statistic.Value>
        <Statistic.Label>Public Servants</Statistic.Label>
      </Statistic>
    </Statistic.Group>

    <Grid divided='vertically' style={{"marginTop": "4em"}}>
      <Header textAlign="center" as="h3" icon>
        <Icon name='line graph' />
        Diseases
        <Header.Subheader>
          Diseases ranked by the number of people infected
        </Header.Subheader>
      </Header>

      <Grid.Row columns={3} centered>
        <Grid.Column width="5" textAlign="center">
          <Header textAlign="center" as="h4">
            Most infectious
            <Header.Subheader>
              Total number of infected
            </Header.Subheader>
          </Header>

          <Table collapsing basic='very' style={{"margin": "2em auto 0 auto"}}>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Disease</Table.HeaderCell>
                <Table.HeaderCell>People infected</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {infectiousDiseases && infectiousDiseases.map((disease: Disease) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as='h4'>
                      <Header.Content>
                        {disease.name.toUpperCase()}
                        <Header.Subheader>{disease.pathogen}</Header.Subheader>
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{disease.infected}</Table.Cell>
                </Table.Row>
              ))}
            </Table.Body>
          </Table>
        </Grid.Column> 

        <Grid.Column width="5" textAlign="center">
          <Header textAlign="center" as="h4">
            Newest
            <Header.Subheader>
              Diseases discovered recently
            </Header.Subheader>
          </Header>

          <Table collapsing basic='very' style={{"margin": "2em auto 0 auto"}}>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Disease</Table.HeaderCell>
                <Table.HeaderCell>Date</Table.HeaderCell>
                <Table.HeaderCell>Country</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {newestDiseases && newestDiseases.map((disease: Disease) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as='h4'>
                      <Header.Content>
                        {disease.name.toUpperCase()}
                        <Header.Subheader>{disease.pathogen}</Header.Subheader>
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{disease.country}</Table.Cell>
                  <Table.Cell>{new Date(disease.first_enc_date).toLocaleDateString()}</Table.Cell>
                </Table.Row>
              ))}
            </Table.Body>
          </Table>
        </Grid.Column>
      
        <Grid.Column width="5" textAlign="center">
          <Header textAlign="center" as="h4">
            Types
            <Header.Subheader>
              Most common disease types
            </Header.Subheader>
          </Header>

          <Table collapsing basic='very' style={{"margin": "2em auto 0 auto"}}>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Type</Table.HeaderCell>
                <Table.HeaderCell>Diseases</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {diseaseTypes && diseaseTypes.map((type: DiseaseType) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as='h4'>
                      <Header.Content>
                        {capitalize(type.name)}
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{type.diseases}</Table.Cell>
                </Table.Row>
              ))}
            </Table.Body>
          </Table>
        </Grid.Column>
      </Grid.Row>
    </Grid>
    </>
  );
}

export default Stats;
