import React, { useState, useEffect } from 'react';
import {
  Card,
  Image,
  Icon,
  Button
} from 'semantic-ui-react';
import { Link } from "react-router-dom";


export type UserProps = {
  name: string;
  surname: string;
  email: string;
  country: string;
  phone: string;
  salary: null | number;
  degree: null | string;
  department: null | string;
}

export default function User(props: UserProps) {
  const [image, setImage] = useState<string>("");
  const [count, setRecordCount] = useState<number>(0);
  const { name, surname, email, country, phone, salary, degree, department } = props;

  useEffect(() => {
    if (department) {
      fetch(`/api/v1/stats/user/${email}`)
        .then((res) => res.json())
        .then((data) => setRecordCount(data["count"]));
    }
    fetch("https://randomuser.me/api/")
      .then((res) => res.json())
      .then((data) => setImage(data.results[0].picture.medium));
  }, [department, email]);

  return (
    <Card>
      <Card.Content>
        <Image
          floated='right'
          size='mini'
          rounded
          src={image}
        />
        <Card.Header>{name + " " + surname}</Card.Header>
        <Card.Meta>{email}</Card.Meta>
        <Card.Meta>{phone}</Card.Meta>
        <Card.Description>From {country}</Card.Description>
        {degree && <Card.Description>{`Doctor, ${degree}`}</Card.Description>}
        {department && <Card.Description>{`Works as a public servant in department ${department}`}</Card.Description>}
      </Card.Content> 
      {department && 
        (<Card.Content extra>
          <Button primary as={Link} to={`/records/${email}`}>
            View {count} records
          </Button>
        </Card.Content>)
      }
      <Card.Content extra>
        <Icon name='dollar' />{salary}/year
      </Card.Content>
      <Card.Content extra>
        <Icon name='map pin' />{country}
      </Card.Content>
    </Card>
  );
}
